@mixin logoParentDiv() {
    .logoParentDiv {
        text-align: center;
        margin-bottom: 20px; } }

@mixin bannerOrder() {
    .bannerParentDiv {
        order: 3; } }

/* For mobiles */
@media only screen and (max-width: 480px) and (max-height: 800px) {
    .bannerParentDiv {
        display: none; }

    @include logoParentDiv(); }

/* For tablets */
@media only screen and (max-width: 640px) and (max-height: 1136px) {
    @include logoParentDiv();

    @include bannerOrder(); }

@media only screen and (max-width: 720px) and (max-height: 1280px) {
    @include logoParentDiv();

    @include bannerOrder(); }
