@mixin buttonParentDiv() {
    .buttonParentDiv {
        text-align: center; } }

/* For mobiles */
@media only screen and (max-width: 480px) and (max-height: 800px) {
    @include buttonParentDiv(); }

/* For tablets */
@media only screen and (max-width: 640px) and (max-height: 1136px) {
    @include buttonParentDiv(); }

@media only screen and (max-width: 720px) and (max-height: 1280px) {
    @include buttonParentDiv(); }
